#header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 6.4rem;
  z-index: 999;
  transition: all 0.3s ease-out;
}
#header .header-inner {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 0 6rem;
}
@media (max-width: 1023px) {
  #header .header-inner {
    flex-wrap: wrap;
    align-items: flex-start;
    padding: 0 1.8rem;
    overflow: hidden;
  }
}
#header .header-inner::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 4px 28px 0px rgba(0, 0, 0, 0.04);
  transition: all 0.3s ease-out;
  opacity: 0;
  z-index: -1;
}
@media (max-width: 1023px) {
  #header .header-inner::after {
    display: none;
  }
}
#header .header-inner .site-logo {
  display: flex;
  align-items: center;
  height: 6.4rem;
}
#header .header-inner .site-logo .site-name {
  display: block;
  width: 10.3rem;
  height: 3.9rem;
  background-size: 100%;
}
@media (max-width: 1023px) {
  #header .header-inner .site-logo .site-name {
    width: 92.02px;
    height: 34.34px;
  }
}
#header .header-inner .site-navi {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 1023px) {
  #header .header-inner .site-navi {
    order: 3;
    align-items: flex-start;
    position: absolute;
    top: 6.4rem;
    height: calc(100vh - 6.4rem);
    margin: 0 -1.8rem;
    overflow: auto;
    display: none;
  }
}
#header .header-inner .site-navi .depth1-list {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 65.4rem;
  transition: all 0.3s ease-out;
}
@media (max-width: 1023px) {
  #header .header-inner .site-navi .depth1-list {
    flex-direction: column;
    width: 100%;
    height: auto;
    padding: 2rem 0 0;
    transition: height 0.3s ease-out;
  }
}
#header .header-inner .site-navi .depth1-list .depth1-item {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  width: 18rem;
  height: 6.4rem;
}
@media (max-width: 1023px) {
  #header .header-inner .site-navi .depth1-list .depth1-item {
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    height: auto;
  }
}
#header .header-inner .site-navi .depth1-list .depth1-item .depth1 {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
