.tab-display.module-a {
  display: flex;
  justify-content: center;
}
.tab-display.module-a .tab-wrap {
  padding: 0.8rem;
  border-radius: 5.6rem;
  background: #095EEF;
}
@media (max-width: 1023px) {
  .tab-display.module-a .tab-wrap {
    padding: 0.56rem;
