  gap: 0.4rem;
}
@media (max-width: 1023px) {
  .tab-display.module-a .tab-wrap .tab-list {
    gap: 0.28rem;
  }
}
.tab-display.module-a .tab-wrap .tab-list .tab-item {
  width: auto !important;
}
.tab-display.module-a .tab-wrap .tab-list .tab-item .tab-text {
  text-transform: uppercase;
  padding: 0.6rem 2rem;
  font-weight: 700;
  font-size: 2rem;
  border-radius: 5.6rem;
  color: rgba(255, 255, 255, 0.6);
  transition: all 0.2s ease-out;
}
@media (max-width: 1023px) {
  .tab-display.module-a .tab-wrap .tab-list .tab-item .tab-text {
    padding: 0.42rem 1.4rem;
