  font-weight: 700;
  border-bottom: 0.3rem solid rgb(5, 104, 204);
}
.tab-display.module-b .tab-wrap .tab-list .tab-item + .tab-item {
  position: relative;
}
.tab-display.module-b .tab-wrap .tab-list .tab-item + .tab-item::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: -4rem;
  width: 0.1rem;
  height: 1.6rem;
  background-color: #d9d9d9;
  transform: translateY(-50%);
}
@media (max-width: 1023px) {
  .tab-display.module-b .tab-wrap .tab-list .tab-item + .tab-item::before {
    top: 45%;
    left: -1.5rem;
    height: 1.2rem;
  }
}
.tab-display.module-b.fixed {
  position: fixed;
  top: 6.4rem;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
  display: flex;
  align-items: flex-end;
  height: 6.4rem;
}
.tab-display.module-b.fixed::after {
  content: "";
  display: block;
  width: 100vw;
  height: 6.4rem;
  z-index: -2;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  background: rgb(255, 255, 255);
  box-shadow: 0px 4px 28px 0px rgba(0, 0, 0, 0.04);
  border-top: 1px solid rgba(203, 203, 203, 0.5);
